<template>
  <v-bottom-sheet
    class="shopping-cart"
    v-model="sheet"
    max-width="500px"
    scrollable
  >
    <v-card min-height="100px" style="border-radius: 16px 16px 0 0">
      <v-card-title v-if="step === 0" class="headline font-weight-black py-4">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              x-small
              :color="isOpenColor"
              style="margin-right: 8px"
              v-bind="attrs"
              v-on="on"
            >
              mdi-checkbox-blank-circle
            </v-icon>
          </template>
          <span v-if="isOpen === null">{{ $t("noData") }}</span>
          <span v-else>{{
            isOpen ? $t("isOpenTrue") : $t("isOpenFalse")
          }}</span>
        </v-tooltip>
        {{ $t("order") }}
        <span class="ml-auto">
          <v-btn
            text
            rounded
            color="primary"
            :disabled="
              !isOpen || order.length === 0 || minimalOrderValue > orderTotal
            "
            @click="step++"
          >
            {{
              minimalOrderValue > orderTotal
                ? `${$t("minimum")} ${minimalOrderValue} ${currencySymbol}`
                : $t("continue")
            }}
          </v-btn>
        </span>
      </v-card-title>
      <v-card-title v-if="step === 1" class="headline font-weight-black py-4">
        <v-btn
          icon
          color="primary"
          small
          @click="step--"
          style="margin-right: 8px"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        {{ $t("contact") }}
        <span class="ml-auto">
          <v-btn
            text
            rounded
            color="primary"
            :disabled="!valid || (!isTakewayAllowed && !isDeliveryAllowed)"
            @click="step++"
          >
            {{ $t("check") }}
            <!-- <v-icon>mdi-arrow-right</v-icon> -->
          </v-btn>
        </span>
      </v-card-title>
      <v-card-title v-if="step === 2" class="headline font-weight-black py-4">
        <v-btn
          icon
          color="primary"
          small
          @click="step--"
          style="margin-right: 8px"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
        {{ $t("summary") }}
        <span class="ml-auto">
          <v-btn
            text
            rounded
            color="#fc010e"
            :disabled="
              !agreementCheckbox || (!isTakewayAllowed && !isDeliveryAllowed)
            "
            :loading="loading"
            @click="sendOrder"
          >
            {{ onlinePayment ? $t("toPay") : $t("toOrder") }}
          </v-btn>
        </span>
      </v-card-title>
      <v-card-title v-if="step === 3" class="headline font-weight-black py-4">
        {{ responseTitle }}
        <v-btn
          icon
          color="primary"
          small
          @click="resetShoppingCart"
          class="ml-auto"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text v-if="step === 1" style="padding-bottom: 0px" class="pb-4">
        <!-- <v-chip-group
          v-model="formMode"
          mandatory
          center-active
          active-class="chip-group-active-class"
        >
          <v-chip filter value="pickup">Osobne</v-chip>
          <v-chip filter value="delivery">Dovies</v-chip>
        </v-chip-group> -->
        <v-chip-group
          v-if="isTakewayAllowed || isDeliveryAllowed"
          v-model="delivery"
          mandatory
          center-active
          active-class="primary--text"
        >
          <v-chip
            v-if="deliveryOptions.takeaway.status && isTakewayAllowed"
            filter
            :value="false"
            >{{ $t("takeaway") }}</v-chip
          >
          <v-chip
            v-if="deliveryOptions.delivery.status && isDeliveryAllowed"
            filter
            :value="true"
            >{{ $t("delivery") }}</v-chip
          >
        </v-chip-group>
        <v-alert
          v-if="!isTakewayAllowed && !isDeliveryAllowed"
          type="error"
          text
          dense
          border="left"
          class="mb-2"
          >{{
            $t("notPossibleToOrderTakeawayOnlyAndDeliveryOnlyItemsTogether")
          }}</v-alert
        >
        <v-form
          ref="form"
          v-model="valid"
          :disabled="!isTakewayAllowed && !isDeliveryAllowed"
        >
          <v-text-field
            dense
            v-model="customer.name"
            :label="$t('customerName')"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            dense
            v-model="customer.email"
            :label="$t('customerEmail')"
            rounded
            filled
            hide-details
            :rules="emailRules"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            dense
            v-model="customer.mobile"
            :label="$t('customerMobile')"
            placeholder="+421 xxx xxx xxx"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            v-if="delivery"
            dense
            v-model="customer.address.street"
            :label="$t('customerAddressStreet')"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-text-field
            v-if="delivery"
            dense
            v-model="customer.address.city"
            :label="$t('customerAddressCity')"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field>
          <v-select
            v-if="delivery"
            :items="deliveryOptions.delivery.zones"
            v-model="customer.address.deliveryZone"
            item-text="name"
            item-value="_id"
            dense
            :label="$t('customerAddressDeliveryZone')"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-select>
          <!-- <v-text-field
            v-if="delivery"
            dense
            v-model="customer.address.zip"
            type="number"
            label="PS"
            rounded
            filled
            hide-details
            :rules="required"
            style="margin-bottom: 8px"
          ></v-text-field> -->
          <v-textarea
            :label="$t('note')"
            v-model="note"
            rows="2"
            rounded
            filled
            hide-details
            style="margin-bottom: 16px"
          ></v-textarea>
          <v-chip-group
            v-model="onlinePayment"
            mandatory
            center-active
            class="paymentOptions"
            active-class="primary--text"
          >
            <span class="subheading mr-2" style="line-height: 32px !important"
              >{{ $t("payment") }}:</span
            >
            <v-chip
              v-if="paymentOptions.cashOnDelivery.status"
              :disabled="!isTakewayAllowed && !isDeliveryAllowed"
              filter
              :value="false"
              >{{ $t("cashOnDelivery") }}</v-chip
            >
            <v-chip
              v-if="paymentOptions.onlinePayments.status"
              :disabled="!isTakewayAllowed && !isDeliveryAllowed"
              filter
              :value="true"
              >{{ $t("onlinePayments") }}</v-chip
            >
          </v-chip-group>
          <v-checkbox
            v-model="saveCustomerCheckbox"
            :label="$t('saveCustomerData')"
            color="primary"
            hide-details
          ></v-checkbox>
        </v-form>
      </v-card-text>
      <v-card-text v-if="step === 0" style="padding-bottom: 0px">
        <v-expansion-panels accordion readonly>
          <v-expansion-panel
            v-for="(dish, i) in order"
            :key="i"
            active-class="active"
          >
            <v-expansion-panel-header hide-actions>
              <v-col
                cols="1"
                sm="3"
                lg="3"
                class="hidden-xs-only image-thumbnail"
              >
                <v-img
                  v-if="dish.imageUrl"
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="75"
                  width="100%"
                  :src="dish.imageUrl"
                ></v-img>
                <v-img
                  v-else
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="75"
                  width="100%"
                  src="https://embeddable.piknik.cc/img/demo/placeholder.gif"
                ></v-img>
              </v-col>
              <v-col cols="9" sm="7">
                <div></div>
                <v-card-title style="padding-left: 0px; padding-top: 0px">
                  <v-list-item-title style="font-size: 20px"
                    ><span style="margin-right: 8px" class="primary--text"
                      >{{ dish.quantity }}x</span
                    >{{ dish.name }}</v-list-item-title
                  >
                </v-card-title>
                <v-card-subtitle style="padding-left: 0px; padding-bottom: 8px">
                  <!-- <span class="d-none full-description">hello{{
                    dish.description
                  }}</span> -->
                  <span
                    v-if="optionsToString(dish) !== dish.description"
                    class="short-description"
                    >{{ optionsToString(dish) }}</span
                  >
                  <v-list-item-subtitle v-else class="short-description">{{
                    dish.description
                  }}</v-list-item-subtitle>
                </v-card-subtitle>
                <v-card-text style="padding-left: 0px; padding-bottom: 0px">
                  <v-tooltip
                    left
                    v-if="
                      !(
                        dish.allowedDeliveryMethods.takeaway &&
                        dish.allowedDeliveryMethods.delivery
                      )
                    "
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <div v-bind="attrs" v-on="on" class="mr-1 d-inline-block">
                        <v-icon
                          small
                          v-if="!dish.allowedDeliveryMethods.takeaway"
                          color="secondary"
                        >
                          mdi-truck-delivery
                        </v-icon>
                        <v-icon
                          small
                          v-if="!dish.allowedDeliveryMethods.delivery"
                          color="secondary"
                        >
                          mdi-walk
                        </v-icon>
                      </div>
                    </template>
                    <span>{{
                      !dish.allowedDeliveryMethods.takeaway
                        ? $t("justForDelivery")
                        : $t("justForTakeaway")
                    }}</span>
                  </v-tooltip>
                  <span class="font-weight-bold" style="padding-bottom: 0px"
                    >{{ (dish.price * dish.quantity).toFixed(2)
                    }}{{ currencySymbol }}</span
                  >&#160;({{ dish.quantity }} x {{ dish.price
                  }}{{ currencySymbol }})
                </v-card-text>
              </v-col>
              <v-col cols="3" sm="2" style="text-align: end">
                <v-btn
                  icon
                  color="#0360ec"
                  small
                  @click.native.stop="$emit('add-dish', { dish })"
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
                <v-btn
                  icon
                  color="#fc010e"
                  small
                  @click.native.stop="$emit('remove-dish', dish)"
                >
                  <v-icon>mdi-minus</v-icon>
                </v-btn>
              </v-col>
            </v-expansion-panel-header>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-card-text>
      <v-card-text v-if="step === 2" style="padding-bottom: 0px">
        <v-alert
          v-if="!isTakewayAllowed && !isDeliveryAllowed"
          type="error"
          text
          dense
          border="left"
          class="mb-2"
          >{{
            $t("notPossibleToOrderTakeawayOnlyAndDeliveryOnlyItemsTogether")
          }}</v-alert
        >
        <OrderSummary
          ref="orderSummary"
          :currencySymbol="currencySymbol"
          :order="order"
          :delivery="delivery"
          :deliveryOptions="deliveryOptions"
          :onlinePayment="onlinePayment"
          :customer="customer"
          :note="note"
          :stripe="stripe"
          :agreementsUrl="agreementsUrl"
          :gdprUrl="gdprUrl"
          v-on:agreement-change="agreementCheckbox = $event"
          v-on:clear-order="$emit('clear-order')"
        />
      </v-card-text>
      <v-card-text v-if="step === 3" style="padding-bottom: 0px">
        <p style="color: rgba(0, 0, 0, 0.87)">
          {{ responseMessage }}
        </p>
      </v-card-text>
      <v-card-title
        v-if="order.length !== 0 && (step === 0 || step === 2)"
        class="font-weight-black py-4"
      >
        {{ $t("total") }}:<span
          style="margin-left: 8px; margin-right: 8px"
          class="primary--text"
          >{{ orderTotalWithDelivery }}{{ currencySymbol }}</span
        >
        <v-tooltip v-if="delivery" top>
          <template v-slot:activator="{ on, attrs }">
            <span v-bind="attrs" v-on="on"
              >({{ deliveryPrice }}{{ currencySymbol }}
              {{ $t("delivery").toLowerCase() }})</span
            >
          </template>
          <span
            v-if="
              deliveryOptions.delivery.zones.filter(
                (zone) => zone._id === this.customer.address.deliveryZone
              )[0]
            "
          >
            {{
              $t("deliveryDetail", {
                zone: deliveryOptions.delivery.zones.filter(
                  (zone) => zone._id === this.customer.address.deliveryZone
                )[0].name,
              })
            }}</span
          >
        </v-tooltip>
      </v-card-title>
    </v-card>
  </v-bottom-sheet>
</template>

<script>
import OrderSummary from "./OrderSummary";

export default {
  name: "ShoppingCart",
  props: [
    "restaurantId",
    "isOpen",
    "currencySymbol",
    "minimalOrderValue",
    "deliveryOptions",
    "paymentOptions",
    "status",
    "order",
    "orderTotal",
    "stripe",
    "agreementsUrl",
    "gdprUrl",
  ],
  components: {
    OrderSummary,
  },
  data: () => ({
    delivery: false,
    onlinePayment: false,
    loading: false,
    customer: {
      name: null,
      email: null,
      mobile: null,
      address: {
        street: null,
        city: null,
        deliveryZone: null,
      },
    },
    note: "",
    step: 0,
    valid: false,
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    required: [(v) => !!v || "Field is required"],
    saveCustomerCheckbox: true,
    agreementCheckbox: false,
    responseTitle: "",
    responseMessage: "",
  }),
  computed: {
    sheet: {
      get() {
        return this.status;
      },
      set() {
        this.$emit("close-shopping-cart");
      },
    },
    isOpenColor: function () {
      if (this.isOpen === true) return "#13e46d";
      else if (this.isOpen === false) return "#fc010e";
      else return "gray";
    },
    deliveryPrice: function () {
      try {
        return this.deliveryOptions.delivery.zones.filter(
          (zone) => zone._id === this.customer.address.deliveryZone
        )[0].price;
      } catch {
        return 0;
      }
    },
    orderTotalWithDelivery: function () {
      if (this.delivery)
        return Math.round((this.orderTotal + this.deliveryPrice) * 1e2) / 1e2;
      return this.orderTotal;
    },
    isTakewayAllowed: function () {
      for (let index = 0; index < this.order.length; index++) {
        if (!this.order[index].allowedDeliveryMethods.takeaway) return false;
      }
      return true;
    },
    isDeliveryAllowed: function () {
      for (let index = 0; index < this.order.length; index++) {
        if (!this.order[index].allowedDeliveryMethods.delivery) return false;
      }
      return true;
    },
  },
  created: function () {
    const params = new URLSearchParams(window.location.search);
    if (params.get("status") === "success") {
      this.responseTitle = this.$t("thankYou");
      // this.responseMessage = `akujeme za Vau objednvku . ${params.get(
      //   "orderId"
      // )}. O stave vaej objednvky vs budeme oskoro informova.`;
      this.responseMessage = this.$t("thankYouForYourOrder", {
        orderId: params.get("orderId"),
      });
      this.step = 3;
    } else if (params.get("status") === "error") {
      this.responseTitle = this.$t("sorry");
      this.responseMessage = this.$t("paymentFailed");
      this.step = 3;
    }
  },
  mounted: function () {
    const customer = localStorage.getItem("customer");
    if (customer) {
      this.customer = JSON.parse(customer);
    }
  },
  methods: {
    optionsToString: function (dish) {
      const options = dish.options;
      let string = "+ ";
      options.forEach((option) => {
        option.values.forEach((value) => {
          if (value.quantity > 1)
            string += `${value.quantity}x ${value.name}, `;
          else string += `${value.name}, `;
        });
      });
      if (string.length === 2) return dish.description;
      return string.slice(0, -2);
    },
    sendOrder: async function () {
      this.loading = true;
      if (this.saveCustomerCheckbox)
        localStorage.setItem("customer", JSON.stringify(this.customer));
      const data = await this.$refs.orderSummary.sendOrder(this.restaurantId);
      if (data && data.id) {
        this.responseTitle = this.$t("thankYou");
        // this.responseMessage = `akujeme za Vau objednvku . ${data.id}. O stave vaej objednvky vs budeme oskoro informova.`;
        this.responseMessage = this.$t("thankYouForYourOrder", {
          orderId: data.id,
        });
        this.step++;
      } else if (data && data.sessionId) {
        this.responseTitle = this.$t("payment");
        this.responseMessage = this.$t("redirecting");
        this.step++;
      } else {
        this.responseTitle = this.$t("sorry");
        this.responseMessage = this.$t("orderFailed");
        this.step++;
      }
    },
    resetShoppingCart: function () {
      this.$emit("close-shopping-cart");
      this.step = 0;
      this.loading = false;
    },
  },
};
</script>

<style>
@media (max-width: 600px) {
  .v-bottom-sheet > * .v-expansion-panel-header {
    padding: 0 !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

@media (min-width: 600px) {
  .v-bottom-sheet > * .v-expansion-panel-header {
    padding: 0 0 !important;
  }
}

.shopping-cart {
  z-index: 1000;
}

.v-dialog {
  box-shadow: none !important;
}

.v-expansion-panel:first-child .col-sm-7 {
  padding-top: 0 !important;
}

.v-expansion-panel:first-child .image-thumbnail {
  padding-top: 0 !important;
}

.v-expansion-panel:last-child .col-sm-7 {
  padding-bottom: 0 !important;
}

.v-expansion-panel:last-child .image-thumbnail {
  padding-bottom: 0 !important;
}

.v-chip-group .v-slide-group__content {
  padding: 0 !important;
  margin-bottom: 8px;
}

.v-chip-group .v-chip {
  margin: 0 8px 0 0 !important;
}

.chip-group-active-class {
  color: #0360ec !important;
  caret-color: #0360ec !important;
}

.paymentOptions .v-slide-group__content {
  margin-bottom: 0 !important;
}
</style>