<template>
  <div>
    <div v-for="category in data" v-bind:key="category._id">
      <div class="category" v-bind:id="category._id">
        <v-card-title
          class="headline font-weight-black"
          style="padding-left: 0; padding-bottom: 0"
        >
          {{ category.name }}
        </v-card-title>
        <v-expansion-panels accordion>
          <v-expansion-panel
            v-for="(dish, i) in category.items"
            :key="i"
            active-class="active"
          >
            <v-expansion-panel-header hide-actions>
              <v-col
                cols="1"
                sm="3"
                lg="2"
                class="hidden-xs-only image-thumbnail"
              >
                <v-img
                  v-if="dish.imageUrl"
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="125"
                  width="100%"
                  :src="dish.imageUrl"
                ></v-img>
                <v-img
                  v-else
                  class="mr-auto"
                  style="border-radius: 8px"
                  height="125"
                  width="100%"
                  src="https://embeddable.piknik.cc/img/demo/placeholder.gif"
                ></v-img>
              </v-col>
              <v-col cols="12" sm="9">
                <v-card-title style="padding-left: 0px; padding-top: 0px"
                  ><span
                    v-if="order.findIndex((item) => item._id === dish._id) > -1"
                    class="primary--text"
                    style="margin-right: 8px"
                    >{{
                      order
                        .filter((item) => item._id === dish._id)
                        .reduce(
                          (accumulator, dish) => accumulator + dish.quantity,
                          0
                        )
                    }}x</span
                  >{{ dish.name }}</v-card-title
                >
                <v-card-subtitle class="pl-0 pb-2"
                  ><span class="full-description"
                    >{{ dish.description }}
                    <v-card-text
                      style="padding: 0px"
                      v-if="dish.allergens.length > 0"
                    >
                      {{ $t("allergens") }}: {{ dish.allergens.join(", ") }}
                    </v-card-text>
                  </span>
                  <v-list-item-subtitle class="short-description"
                    >{{ dish.description }}
                  </v-list-item-subtitle>
                </v-card-subtitle>
                <v-card-text style="padding-left: 0px; padding-bottom: 0px">
                  <v-tooltip
                    left
                    v-if="
                      !(
                        dish.allowedDeliveryMethods.takeaway &&
                        dish.allowedDeliveryMethods.delivery
                      )
                    "
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <div v-bind="attrs" v-on="on" class="mr-1 d-inline-block">
                        <v-icon
                          v-if="!dish.allowedDeliveryMethods.takeaway"
                          color="secondary"
                          small
                        >
                          mdi-truck-delivery
                        </v-icon>
                        <v-icon
                          v-if="!dish.allowedDeliveryMethods.delivery"
                          color="secondary"
                          small
                        >
                          mdi-walk
                        </v-icon>
                      </div>
                    </template>
                    <span>{{
                      !dish.allowedDeliveryMethods.takeaway
                        ? $t("justForDelivery")
                        : $t("justForTakeaway")
                    }}</span>
                  </v-tooltip>
                  <span class="font-weight-bold" style="padding-bottom: 0px"
                    >{{ calculateDishPrice(dish) }}{{ currencySymbol }}</span
                  >&#160;/{{ dish.amount }}
                  <v-btn
                    text
                    rounded
                    color="primary"
                    small
                    :disabled="!mandatoryOptionsProvided(dish)"
                    @click.native.stop="
                      $emit('add-dish', {
                        dish,
                        options: selectedOptions.filter(
                          (e) => dish.options.indexOf(e._id) !== -1
                        ),
                        price: calculateDishPrice(dish),
                      })
                    "
                  >
                    {{ $t("add") }}
                  </v-btn>
                </v-card-text>
              </v-col>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-col cols="12" style="padding-top: 0px">
                <v-img
                  v-if="dish.imageUrl && dish.options.length !== 0"
                  class="d-sm-none mb-4"
                  style="border-radius: 8px"
                  max-height="250"
                  :src="dish.imageUrl"
                ></v-img>
                <v-img
                  v-else-if="dish.imageUrl"
                  class="d-sm-none"
                  style="border-radius: 8px"
                  max-height="250"
                  :src="dish.imageUrl"
                ></v-img>
                <v-card-text class="hidden-xs-only" style="padding: 0px">
                  {{ dish.description }}
                </v-card-text>
                <v-card-text
                  class="hidden-xs-only"
                  style="padding: 0px"
                  v-if="dish.allergens.length > 0"
                >
                  {{ $t("allergens") }}: {{ dish.allergens.join(", ") }}
                </v-card-text>
                <Options
                  v-if="dish.options.length !== 0"
                  :currencySymbol="currencySymbol"
                  :dish="dish"
                  :options="options"
                  :selectedOptions="selectedOptions"
                  class="options"
                  v-on:add-option="addOption($event)"
                  v-on:remove-option="removeOption($event)"
                />
              </v-col>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </div>
    </div>
  </div>
</template>

<script>
import Options from "./Options";

export default {
  name: "Dishes",
  props: ["currencySymbol", "data", "options", "order"],
  components: {
    Options,
  },
  data: () => ({
    selectedOptions: [],
  }),
  methods: {
    addOption: function (event) {
      const option = event.option;
      const value = event.value;
      const index = this.selectedOptions.findIndex((e) => e._id === option._id);
      if (index === -1) {
        this.selectedOptions.push({
          _id: option._id,
          values: [
            {
              _id: value._id,
              name: value.name,
              price: value.price,
              quantity: 1,
            },
          ],
        });
      } else {
        const index2 = this.selectedOptions[index].values.findIndex(
          (e) => e._id === value._id
        );
        if (index2 === -1) {
          this.selectedOptions[index].values.push({
            _id: value._id,
            name: value.name,
            price: value.price,
            quantity: 1,
          });
        } else {
          this.selectedOptions[index].values[index2].quantity++;
        }
      }
    },
    removeOption: function (event) {
      const option = event.option;
      const value = event.value;
      const index = this.selectedOptions.findIndex((e) => e._id === option._id);
      if (index !== -1) {
        const index2 = this.selectedOptions[index].values.findIndex(
          (e) => e._id === value._id
        );
        if (index2 !== -1) {
          const quantity = this.selectedOptions[index].values[index2].quantity;
          if (quantity === 1)
            this.selectedOptions[index].values.splice(index2, 1);
          else this.selectedOptions[index].values[index2].quantity--;
        }
      }
    },
    mandatoryOptionsProvided: function (dish) {
      try {
        const options = this.options
          .filter((e) => dish.options.indexOf(e._id) !== -1)
          .filter((e) => e.minimumSelections >= 1);

        for (let i = 0; i < options.length; i++) {
          const index = this.selectedOptions.findIndex(
            (e) => e._id === options[i]._id
          );

          if (
            !(
              this.selectedOptions[index].values.length >=
              options[i].minimumSelections
            )
          )
            return false;
        }
        return true;
      } catch {
        return false;
      }
    },
    calculateDishPrice: function (dish) {
      let price = dish.price;
      const options = this.selectedOptions.filter(
        (e) => dish.options.indexOf(e._id) !== -1
      );

      options.forEach((option) => {
        let optionsTotal = 0;
        option.values.forEach((value) => {
          optionsTotal += value.quantity * value.price;
        });
        price += optionsTotal;
      });

      return price;
    },
  },
};
</script>

<style>
.v-card__title {
  word-break: unset !important;
}

.image-thumbnail {
  padding-right: 12px !important;
}

.v-expansion-panel:before {
  box-shadow: none !important;
}

.full-description {
  display: none;
}

.options {
  margin-top: 16px;
}

@media (max-width: 600px) {
  .v-expansion-panel-header {
    padding: 0 !important;
    padding-left: 8px !important;
    padding-right: 8px !important;
  }
  .v-expansion-panel-content__wrap {
    padding: 0 !important;
    padding-left: 8px !important;
    padding-right: 8px !important;
  }

  .active > * .full-description {
    display: block !important;
  }

  .active > * .short-description {
    display: none !important;
  }

  .options {
    margin-top: 0px;
  }
}

@media (min-width: 600px) {
  .v-expansion-panel-header {
    padding: 16px 16px !important;
  }

  .v-expansion-panel-content__wrap {
    padding-left: 16px !important;
    padding-right: 16px !important;
  }
}

.dailyMenu .v-card__subtitle {
  padding-bottom: 8px !important;
}
</style>