<template>
  <div>
    <!-- <v-col cols="12" style="padding-top: 0">
      <span class="font-weight-bold">{{ customer.name }}</span
      ><br />
      <span>{{ customer.email }}</span
      ><br />
      <span>{{ customer.mobile }}</span
      ><br />
      <span v-if="true"
        >{{ customer.address.street }}, {{ customer.address.zip }}
        {{ customer.address.city }}</span
      >
      <span v-else>Osobn vyzdvihnutie</span>
    </v-col> -->

    <v-card
      class="mx-auto mb-4"
      flat
      rounded
      color="rgba(0, 0, 0, 0.06)"
      style="border-radius: 28px !important"
    >
      <v-list-item three-line>
        <v-list-item-avatar color="primary">
          <span class="white--text">{{
            customer.name
              .split(" ")
              .map((n) => n[0])
              .join("")
          }}</span>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="">{{ customer.name }}</v-list-item-title>
          <v-list-item-subtitle>{{ customer.email }}</v-list-item-subtitle>
          <v-list-item-subtitle class="mb-2">{{
            customer.mobile
          }}</v-list-item-subtitle>
          <v-list-item-subtitle v-if="delivery"
            >{{ customer.address.street }}, {{ customer.address.zip }}
            {{ customer.address.city }}</v-list-item-subtitle
          >
          <v-list-item-subtitle v-else>{{
            $t("personalPickup")
          }}</v-list-item-subtitle>
          <!-- <v-list-item-subtitle>Platba {{ onlinePayment ? 'kartou online' : 'pri prevzat' }}</v-list-item-subtitle> -->
        </v-list-item-content>
      </v-list-item>
    </v-card>

    <v-checkbox
      v-model="agreement"
      class="pa-0 ma-0 mb-4"
      hide-details
      color="primary"
      v-on:click="$emit('agreement-change', agreement)"
    >
      <template v-slot:label>
        <div>
          {{ $t("aggreeWith") }}
          <a
            class="primary--text text-decoration-none"
            :href="agreementsUrl"
            target="_blank"
            @click.stop
            rel="noopener noreferrer"
            >{{ $t("withOrderingAgreements") }}</a
          >
          {{ $t("andWith") }}
          <a
            class="primary--text text-decoration-none"
            :href="gdprUrl"
            target="_blank"
            @click.stop
            rel="noopener noreferrer"
            >{{ $t("withGDPRAgreements") }}</a
          >.
        </div>
      </template></v-checkbox
    >

    <!-- <div
      class="mb-4"
      style="background-color: rgba(0, 0, 0, 0.06); width: 100%; height: 12px"
    ></div> -->
    <!-- <v-img class="mb-4" height="6px" src="../assets/devider2.svg"></v-img> -->
    <v-img
      class="mb-4"
      height="6px"
      src="https://embeddable.piknik.cc/img/devider.svg"
    ></v-img>

    <v-expansion-panels accordion readonly>
      <v-expansion-panel
        v-for="(dish, i) in order"
        :key="i"
        active-class="active"
      >
        <v-expansion-panel-header hide-actions>
          <!-- <v-col cols="1" sm="3" lg="3" class="hidden-xs-only image-thumbnail">
          <v-img
            class="mr-auto"
            style="border-radius: 8px"
            height="75"
            width="100%"
            src="https://i.imgur.com/biNJ06H.jpg"
          ></v-img>
        </v-col> -->
          <v-col cols="12">
            <div></div>
            <v-card-title style="padding-left: 0px; padding-top: 0px">
              <v-list-item-title style="font-size: 20px"
                ><span style="margin-right: 8px" class="primary--text"
                  >{{ dish.quantity }}x</span
                >{{ dish.name }}</v-list-item-title
              >
            </v-card-title>
            <v-card-subtitle style="padding-left: 0px; padding-bottom: 0">
              <!-- <span class="d-none full-description">{{
                dish.description
              }}</span>
              <v-list-item-subtitle class="short-description">{{
                dish.description
              }}</v-list-item-subtitle> -->
              <span
                v-if="optionsToString(dish) !== dish.description"
                class="short-description"
                >{{ optionsToString(dish) }}</span
              >
              <v-list-item-subtitle v-else class="short-description">{{
                dish.description
              }}</v-list-item-subtitle>
            </v-card-subtitle>
            <!-- <v-card-text style="padding-left: 0px; padding-bottom: 0px">
            <span class="font-weight-bold" style="padding-bottom: 0px"
              >{{ (dish.price * dish.quantity).toFixed(2) }}</span
            >&#160;({{ dish.quantity }} x {{ dish.price }})
          </v-card-text> -->
          </v-col>
          <!-- <v-col cols="3" sm="2" style="text-align: end">
          <v-btn
            icon
            color="#0360ec"
            small
            @click.native.stop="$emit('add-dish', dish)"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>
          <v-btn
            icon
            color="#fc010e"
            small
            @click.native.stop="$emit('remove-dish', dish)"
          >
            <v-icon>mdi-minus</v-icon>
          </v-btn>
        </v-col> -->
        </v-expansion-panel-header>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
import { API_URL } from "../api.js";

export default {
  name: "Header",
  props: [
    "currencySymbol",
    "order",
    "delivery",
    "customer",
    "note",
    "onlinePayment",
    "stripe",
    "agreementsUrl",
    "gdprUrl"
  ],
  data: () => ({
    agreement: false,
  }),
  methods: {
    optionsToString: function (dish) {
      const options = dish.options;
      let string = "+ ";
      options.forEach((option) => {
        option.values.forEach((value) => {
          if (value.quantity > 1)
            string += `${value.quantity}x ${value.name}, `;
          else string += `${value.name}, `;
        });
      });
      if (string.length === 2) return dish.description;
      return string.slice(0, -2);
    },
    sendOrder: async function (restaurantId) {
      try {
        const requestOptions = {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            restaurantId: `${restaurantId}`,
            delivery: this.delivery,
            customer: this.customer,
            items: this.order,
            note: `${this.note}`,
            onlinePayment: this.onlinePayment,
          }),
        };

        const response = await fetch(`${API_URL}/order/create`, requestOptions);
        const data = await response.json();

        if (this.onlinePayment) {
          this.stripe.redirectToCheckout({
            sessionId: data.sessionId,
          });
          return data;
        } else {
          this.agreement = false;
          this.$emit("agreement-change", false);
          this.$emit("clear-order");
          return data;
        }
      } catch {
        this.agreement = false;
        this.$emit("agreement-change", false);
        this.$emit("clear-order");
        return null;
      }
    },
  },
};
</script>

<style>
.v-expansion-panel:first-child .col {
  padding-top: 0 !important;
}

.v-expansion-panel:last-child .col {
  padding-bottom: 0 !important;
}

.custom-blue--text {
  color: #0360ec !important;
  caret-color: #0360ec !important;
  text-decoration: none !important;
}
</style>